@import "../../styles/mixins";

.contacts {
    margin-bottom: 80px;
    position: relative;
    @include md {
        margin-bottom: 20px;
        background-color: #282d33;
    }
    &_container {
        @include container;
    }
    &_overlay {
        background-color: rgba(#1d2025, .5);
        z-index: 3;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: fixed;
    }
    h2 {
        @include heading;
    }
    form {
        background-color: #282d33;
        width: calc(100% - 100px);
        color: #fff;
        padding: 60px 0 60px 280px;
        position: relative;
        display: flex;
        flex-direction: column;
        @include md {
            padding: 40px 10px;
            width: 100%;
        }
        input, textarea {
            max-width: 400px;
            background-color: transparent;
            border: none;
            border-bottom: 2px solid rgba(#fff, .5);
            padding: 7px;
            color: #fff;
        }
        input {
            margin-bottom: 30px;
        }
        textarea {
            margin-bottom: 50px;
            @include md {
                margin-bottom: 40px;
            }
        }
        button {
            @include yellow_button;
        }
        ::placeholder {
            color: white;
        }
    }
    &_form {
        display: flex;
        justify-content: flex-end;
        h4 {
            font-weight: 300;
            &:last-of-type {
             margin-bottom: 20px;   
            }
            @include md {
                font-size: 14px;
            }
        }
        &_button {
            text-align: center;
        }
        &_absolute {
            position: absolute;
            background-color: #ffc91e;
            width: 300px;
            height: 375px;
            top: 40px;
            left: -100px;
            padding: 40px 40px 0 30px;
            h5 {
                font-weight: bold;
                text-transform: uppercase;
                color: #282d33;
                margin-bottom: 20px;
                font-size: 18px;
            }
        }
        &_mobile {
            background-color: #ffc91e;
            padding: 40px;
            width: 100%;
            height: 310px;
            h5 {
                font-weight: bold;
                text-transform: uppercase;
                color: #282d33;
                margin-bottom: 20px;
                font-size: 18px;
            }
        }
    }
    &_list {
        margin-bottom: 40px;
        &_item {
            display: flex;
            align-items: center;
            font-size: 14px;
            margin-bottom: 30px;
            &:last-of-type {
                margin-bottom: 0;
            }
            img {
                margin-right: 25px;
            }
            p {
                color: #282d33;
            }
        }
    }
    &_socials {
        display: flex;
        &_item {
            width: 24px;
            height: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            background-color: rgba(255,255,255,.2);
            border-radius: 3px;
            margin-right: 12px;
        }
    }
    
}

.modal {
  width: 500px;
  height: 400px;
display: flex;
flex-direction: column;
background-color: #1d2025;
box-shadow: 0 4px 10px rgba(black, 0.5);
position: fixed;
padding: 25px 80px 40px 80px;
justify-content: space-between;
text-align: center;
color: #fff;
left: 50%;
top: 50%;
transform: translate(-50%, -50%);
z-index: 10;
h3 {
  font-size: 24px;
}
p {
  font-size: 14px;
  line-height: 20px;
}
&_cross {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}
&_button {
  button {
    @include yellow_button;
    // border: 1px solid #52d1e0;
    width: 140px;
  }
}
}